import { Component, ChangeDetectorRef} from '@angular/core';
import { IonicToastService } from '../services/ionic-toast.service';
import { IonAlertService } from '../services/ion-alert.service';
import { ModalController } from '@ionic/angular';
import { DOCUMENT } from '@angular/common';
import { Inject } from '@angular/core';
import { Router } from '@angular/router';
import JSONContract from '../thewall.json';
import { ModaldonacionComponent } from '../componentes/modaldonacion/modaldonacion.component';
import { ModalsmsComponent } from '../componentes/modalsms/modalsms.component';
import {Subject} from 'rxjs'

// web3 librerias
import Web3 from 'web3';



@Component({
  selector: 'app-home',
  templateUrl: 'home.page.html',
  styleUrls: ['home.page.scss'],
})
export class HomePage {
 
  filtroNombre:string[]=[];
  resultadosFiltro:string[] = [...this.filtroNombre];
  visibleFiltro: boolean=false;
  
  web3: any;
  chainID:any;
  GoerliChainId:any='0x5';
  window: any;
  arrayNFT:string[]=[];
  arrayNFTFiltrado:string[]=[...this.arrayNFT];
  arrayNFTExtra:string[]=[];
  arraySMS:string[]=[];
  provider:any;
  wallet:any = {
    address: '',
    balance:''
  }
  eventObservableNFTLoaded$: Subject<string[]>;

  contractAddress:any;
  contract:any;
  TokenActivo:number=0;
  TokenLikes:any=0;
  TokenBalance:any=0;
  TokenSMS:any=0;
  BotonSMS:Boolean=false;

  constructor(private cd: ChangeDetectorRef,private modalCtrl: ModalController,@Inject(DOCUMENT) private document: Document,private ionicToastService: IonicToastService,private ionicAlertService: IonAlertService,private router: Router) {

    this.eventObservableNFTLoaded$=new Subject();
    this.window = document.defaultView;
    sessionStorage.setItem('address', '');  //variable global de sesion para mantener wallet
    sessionStorage.setItem('chainid', this.GoerliChainId);  
    localStorage.setItem('contratoTheWall', "0x9D9333d1fED5e4dB78552c687Bf899044122c105");  

    this.contractAddress=localStorage.getItem("contratoTheWall");

    this.web3 = new Web3;
     this.web3.setProvider(
     new this.web3.providers.HttpProvider('https://goerli.infura.io/v3/cdb31d0cd0aa40d894216d4e272f6bb4')
     );
     this.contract = new this.web3.eth.Contract(JSONContract.abi, this.contractAddress);
     
  }

  ngOnInit() {
    
    this.provider=this.window.ethereum; // METAMASK
    this.ionicAlertService.eventoBotonRed.subscribe(counter => {
      this.cambiaRed();
    });
    this.ionicAlertService.eventoBotonRedCancelar.subscribe(counter => {
      this.cancelarConexion();
    });

    this.chainID=sessionStorage.getItem("chainid");

     this.cargaNFTs();
  }

  async cargaNFTs(){

    let check=0;
    this.arrayNFT= await this.contract.methods.GetTokenPixels().call().catch((err)=>{
      this.ionicAlertService.showAlert("Error de Conexin","No ha podido conectarse al Smart Contract");
      check=1;
    });
    if (check==1)
    return;

    this.arrayNFTExtra= await this.contract.methods.GetTokenExtra().call().catch((err)=>{
      this.ionicAlertService.showAlert("Error de Conexin","No ha podido conectarse al Smart Contract");
      check=1;
    });
    if (check==1)
    return;
    
    this.eventObservableNFTLoaded$.next(this.arrayNFT);
    this.cargaFiltro(this.arrayNFT);
    
   
  }

  cargaFiltro(datos)
  {
    for (let x=0;x<datos.length;x+=1)
    {
      this.filtroNombre.push(datos[x][1]);
    }
  }

  async loginMetamask() {
    if (!this.provider) {
      return this.ionicAlertService.showAlert(" Atencin !","Metamask no est instalado");
    }
    try {
      const accounts = await this.provider.request({ method: 'eth_requestAccounts' });
      this.compruebaRed();
      this.wallet.address=accounts[0];
      sessionStorage.setItem('address', this.wallet.address);
      this.getBalance(this.wallet.address);
    } catch (error) {
      if (error.code === 4001) {
        this.ionicAlertService.showAlert("OPS!","Acceso a Metamask cancelado");
      }
    }
  }

  async getBalance(address:string) {
    this.wallet.address = address;
    this.wallet.balance = await this.web3.eth.getBalance(address).then((result:any) => {
      return this.web3.utils.fromWei(result, 'ether');
    });
  }

  async compruebaRed(){

    const chainId = await this.provider.request({ method: 'eth_chainId' });
   
    if(chainId !== this.chainID){
      this.ionicAlertService.showAlertRedMetamask(" No ests conectado a la red Goerli !");
      }

  }

  async cambiaRed() {
    try {
      await this.provider.request({
        method: 'wallet_switchEthereumChain',
        params: [{ chainId: this.chainID}],
      });
      this.ionicAlertService.showAlert(" OK !","Red Goerli configurada correctamente.");
    } catch (switchError) {
      if (switchError.code === 4902) {
        this.ionicAlertService.showAlert(" ERROR !","No tienes la red Goerli configurada en Metamask");
      }
      this.ionicAlertService.showAlert(" ERROR !","Imposible cambiar de red");
    }
  }

  async cancelarConexion() 
  {
    this.wallet.address='';
    sessionStorage.setItem('address', '');
  }

  desconectar() {
  
    this.wallet.address='';
    
  }

  iraMintear(){
    
    if (!this.wallet.address)
      this.ionicAlertService.showAlert("OPS!","Por favor conecta primero tu Wallet a TheWall");
    else
       this.router.navigate(['/mintear']);
  }

  filtra(event){
    const textoB = event.target.value.toLowerCase();
    if (textoB && textoB!='')
    {
    this.visibleFiltro=true;
    this.resultadosFiltro = this.filtroNombre.filter(d => d.toLowerCase().indexOf(textoB) > -1);
    if (this.resultadosFiltro.length<1)
    this.visibleFiltro=false;
    }
    else
    this.visibleFiltro=false;
  }

  cancelaBusqueda()
  {
    this.visibleFiltro=false;
  }

  busca(event){

    let name;
    let cumple;
   
    const textoB = event.target.value.toLowerCase();
    this.arrayNFTFiltrado=[];

    for (let x=0;x<this.arrayNFT.length;x+=1)
    {
      name=this.arrayNFT[x][1];
      cumple=name.toLowerCase().indexOf(textoB);

      if (cumple>-1)  //indeox devuelve un valor mayor de -1 si no encuentra nada y un valor X de la posicin del caracter en caso afirmativo
      this.arrayNFTFiltrado.push(this.arrayNFT[x]);
    }

    event.target.value="";
    this.eventObservableNFTLoaded$.next(this.arrayNFTFiltrado);
    this.visibleFiltro=false;
  }

  MyNFT()
  {

    let addressaux
    let cumple;
    let check=0;
   
    const address = this.wallet.address;
    this.arrayNFTFiltrado=[];

    for (let x=0;x<this.arrayNFT.length;x+=1)
    {
      addressaux=this.arrayNFT[x][2];
      cumple=addressaux.toLowerCase().indexOf(address);

      if (cumple>-1)  //indeox devuelve un valor mayor de -1 si no encuentra nada y un valor X de la posicin del caracter en caso afirmativo
      {
      this.arrayNFTFiltrado.push(this.arrayNFT[x]);
      check=1;
      }
    }

    if (check==0)
      this.ionicAlertService.showAlert(" OPS !","No posees ningun NFT de esta coleccin");
    else
       {
        this.eventObservableNFTLoaded$.next(this.arrayNFTFiltrado);
        this.visibleFiltro=false;
       }
  }

  cambiaSlide(event)
  {
    this.TokenActivo=event.tokenid;
    this.datosNFTExtra();
    var id=document.getElementById("capaInFooter");
    id.style.opacity="1";
  }

  cambiandoSlide()
  {
    var id=document.getElementById("capaInFooter");
    id.style.opacity="0.3";
  }

 
  async meGusta()
  {
    if (!this.wallet.address)
     return this.ionicAlertService.showAlert("OPS!","Por favor conecta primero tu Wallet a TheWall");
  
      const transactionParameters = {
        nonce: '0x00', 
        to: this.contractAddress, 
        from: this.wallet.address, 
        value: '0', 
        data:this.contract.methods.like(this.TokenActivo).encodeABI(),
        chainId: this.chainID, 
      };
  
     const txHash = await this.provider.request({
      method: 'eth_sendTransaction',
      params: [transactionParameters],
       }).then((result) => {
        this.ionicAlertService.showAlert("MetaMask est procesando la solicitud","Cuando haya terminado recibirs una confirmacon en Metamask");
      }).catch((err)=>{
          if (err.code == 4001)
          this.ionicAlertService.showAlert("MetaMask","La transaccin ha sido cancelada");
       })

  }

  compruebaAddress() :Boolean
  {
   let check=true;
     if ( this.arrayNFT[this.TokenActivo-1][2].toLowerCase()==this.wallet.address)
     {
      this.ionicAlertService.showAlert("OPS!","No puedes donarte a ti mismo ;)");
      check=false;
     }
     return check;
  }

  async patrocinio()
  {
    if (!this.wallet.address)
      return this.ionicAlertService.showAlert("OPS!","Por favor conecta primero tu Wallet a TheWall");
   
        const modal= await this.modalCtrl.create({
          component:ModaldonacionComponent,
          componentProps: {
            tokenID: this.TokenActivo,
            saldo:this.wallet.balance
          }
        });

        let comprueba=this.compruebaAddress();
        if(!comprueba)
        return;

        await modal.present();
        const {data} = await modal.onDidDismiss(); // hasta que no se acepta o descarta...
      
        if (data.orden=="confirmar")
        {

         let importeWei=this.web3.utils.toWei(data.importe, 'ether');
         let importeWeiHex=this.web3.utils.toHex(importeWei); // Metamask trabaja en HEX
        
          const transactionParameters = {
            nonce: '0x00', 
            to: this.contractAddress,
            from: this.wallet.address, 
            value: importeWeiHex,
            data:this.contract.methods.donar(this.TokenActivo,data.texto).encodeABI(),
            chainId: this.chainID, 
          };

          const txHash = await this.provider.request({
          method: 'eth_sendTransaction',
          params: [transactionParameters],
           }).then((result) => {
            this.ionicAlertService.showAlert("MetaMask est procesando la solicitud","Cuando haya terminado recibirs una confirmacon en Metamask");
          }).catch((err)=>{
              if (err.code == 4001)
              this.ionicAlertService.showAlert("MetaMask","La transaccin ha sido cancelada");
           })       
          }
  }

  async tokenMensajes()
  {

    let check=0;

    this.arraySMS= await this.contract.methods.GetTokenSMS(this.TokenActivo).call().catch((err)=>{
      this.ionicAlertService.showAlert("Error de Conexin","No ha podido conectarse al Smart Contract");
      check=1;
    });
    if (check==1)
    return;


    const modal= await this.modalCtrl.create({
      component:ModalsmsComponent,
      componentProps: {
        tokenArraySMS: this.arraySMS,
        tokenID:this.TokenActivo
      }
    });

    await modal.present();
    const {data} = await modal.onDidDismiss(); // hasta que no se acepta o descarta...

  }


  datosNFTExtra()
  {

    let wei=this.arrayNFTExtra[this.TokenActivo-1][2];
    this.TokenBalance=this.web3.utils.fromWei(wei, 'ether');
    this.TokenLikes=this.arrayNFTExtra[this.TokenActivo-1][1];
    this.TokenSMS=this.arrayNFTExtra[this.TokenActivo-1][2];

    if (this.TokenSMS!="0")
    this.BotonSMS=true;
    else
    this.BotonSMS=false;

    this.cd.detectChanges();
    

  }

  showMyToast(mensaje:any) {
    this.ionicToastService.showToast(mensaje);
  }
  showMyToastMy(mensaje:any) {
    this.ionicToastService.showToastMy(mensaje);
  }
  hideMyToast() {
    this.ionicToastService.HideToast();
  }


}


