import { Injectable } from '@angular/core';
import { withRouterConfig } from '@angular/router';
import { ToastController } from '@ionic/angular';

@Injectable({
  providedIn: 'root'
})
export class IonicToastService {

  private myToast: any;

  constructor( private toast: ToastController) { }

  showToast(mensaje:any) {
    this.myToast = this.toast.create({
      message: mensaje,
      duration:700,
      position: 'bottom',
      animated:true,
      cssClass:"claseToast"
    }).then((toastData) => {
      console.log(toastData);
      toastData.present();
    });
  }

  showToastMy(mensaje:any) {
    this.myToast = this.toast.create({
      message: mensaje,
      duration:1200,
      position: 'top',
      animated:true,
      cssClass:"claseToastMy"
    }).then((toastData) => {
      console.log(toastData);
      toastData.present();
    });
  }

  HideToast() {
    this.myToast = this.toast.dismiss();
  }
}