import { Component, OnInit,Input, Output, EventEmitter, ViewChild, ViewEncapsulation} from '@angular/core';
import SwiperCore, {Navigation,Pagination,Keyboard,Manipulation} from "swiper";
import { LoadingController } from '@ionic/angular';
import { Observable } from 'rxjs';
import { Subscription } from 'rxjs';


// install Swiper modules
SwiperCore.use([Navigation, Pagination, Keyboard,Manipulation]);

@Component({
  selector: 'app-swiperkuai',
  templateUrl: './swiperkuai.component.html',
  styleUrls: ['./swiperkuai.component.scss'],
  encapsulation:ViewEncapsulation.None,
})
export class SwiperkuaiComponent implements OnInit {

  @ViewChild('newSwiper') newSwiper: any;
  loading:any;

  private eventsSubscription: Subscription;
  contractAddress:any;
  @Input() eventoCarga: Observable<string[]>;
  @Output() eventoCambiaSlide = new EventEmitter<{tokenid: string}>();
     // new EventEmitter<{make: string, name: string}>();
  @Output() eventoCambiando = new EventEmitter<any>();
 
 
  constructor(private loadingCtrl: LoadingController) { }

  ngOnInit() {
    this.eventsSubscription = this.eventoCarga.subscribe((datos) => this.cargaElementos(datos));
    this.contractAddress=localStorage.getItem("contratoTheWall");
  }
 
  async cargaElementos(datos)
  {

    // antes vaciamos todos los slides del anterior resultado de busquedas 
    this.newSwiper.swiperRef.removeAllSlides();
    let load=await this.showLoading();

    let tokenid;
    let name;
    let owner;
    let tokenuri;
    let pixels;
    let size;
    let colors;
    let plantillaHTML;
    let nftpixelsHTML;
    let openseaLink;

    for (let x=0;x<datos.length;x+=1)
    {
      nftpixelsHTML="";
      plantillaHTML="";
      tokenid=datos[x][0];
      name=datos[x][1];
      owner=datos[x][2];
      tokenuri=datos[x][3];
      pixels=datos[x][4];
      size=datos[x][5];
      colors=datos[x][6];

      openseaLink="https://testnets.opensea.io/assets/goerli/"
      openseaLink=openseaLink + this.contractAddress + "/";
      openseaLink=openseaLink+tokenid;
      

      plantillaHTML="<div class='swiper-slide'><div id='capaMaestraNFT'><div class='tokenid'>TOKEN #";
      plantillaHTML+=tokenid+"</div><div id='tokenid' style='display:none'>"+tokenid+"</div>";
      plantillaHTML+="<div class='nombreNFT'>"+name+"</div>";
      plantillaHTML+="<div id='outerCanvasNFT'><div id='ficha'><div class='divficha'><div class='divOwner'><ion-icon name='person-outline' class='iconoOwner'></ion-icon><div id='addressOwner'>"+owner+"</div></div><div class='divOpensea'><img src='/assets/graficos/opensea.png' id='logoOpensea'><a href="+openseaLink+"><span class='spanLink'>Comercia con <u><b>"+name+"</b></u> en Opensea</span></a></div></div></div><div class='canvasNFT'>";

      // generamos el nft con la info de los pixels : tamao, colores, y pixels.

      nftpixelsHTML+="<div class='filaPixels'>";

      for (let p=0;p<pixels.length;p+=1)
      {
          if (p!=0 && p%size[0]==0) // cambio fila  
          {
          nftpixelsHTML+="</div>";
          nftpixelsHTML+="<div class='filaPixels'>";
          }
          nftpixelsHTML+="<div class='pixel' style='background-color:"+colors[pixels[p]]+"'><img src='/assets/graficos/pixelAux.png' class='pixelAux'></div>";
      }

      plantillaHTML+=nftpixelsHTML;

      plantillaHTML+="</div></div>";
      plantillaHTML+="</div></div>";
      this.newSwiper.swiperRef.appendSlide(plantillaHTML);
    }

    this.newSwiper.swiperRef.slideTo(0);
    this.newSwiper.swiperRef.update();
    this.loading.dismiss();
  }

  cambiaSlide() {
  
  let tokenID= document.querySelector(".swiper-slide-active #tokenid").innerHTML;
  let addressOwner= document.querySelector(".swiper-slide-active #addressOwner").innerHTML;
  
  this.eventoCambiaSlide.emit({tokenid:tokenID});
   
  }

  cambiando() {
    let tokenid= document.querySelector(".swiper-slide-active #tokenid").innerHTML;
    this.eventoCambiando.emit();
    }

  ngAfterViewInit(): void {


  }

  async showLoading() {
    this.loading = await this.loadingCtrl.create({
      message: 'Cargando...',
    });
    this.loading.present();
  }


}
